<template>
    <div :class="['page','container']">
        
        <div class="archives">
            <div class="archives_count">{{countWord}}</div>
            <section v-for="group in articleGroup">
                <div class="archives_title">
                    <strong>{{group.year}}</strong>
                </div>
                <div class="archives_item" v-for="article in group.articles">
                    <router-link class="archives_link" :to="{ name: 'articleDetail', params: { title: article.title }}"><span class="archives_date">{{article.created_at | dateFormat}}</span> {{article.title}}</router-link>
                
                </div>
            </section>
        </div>
        <pagination :page="page" :active="active" name="articleList"></pagination>
    </div>
</template>
<script>
    import xm from '../base/function'
    import pagination from '@/components/Pagination'
    
    export default{
        name:"articleList",
        components:{pagination},
        mounted:function(){
            this.getData();
        },
        methods:{
            getData: function () {
                let postData = this.$route.params;
            
                if (postData.active == ''){
                    postData.active = 1;
                }
            
                this.$http.post('blog/archives',postData).then((res)=>{
                    return res.json();
                }).then((res)=>{
                    var lists = res['lists'];
                    var page = res['page'];
                
                    var articleGroup = [];
                    var cursor = 0;
                
                    articleGroup[cursor] = {
                        year: lists && lists[0] && lists[0].year,
                        articles: []
                    };
                
                    lists.forEach((val)=>{
                        if (val.year !== articleGroup[cursor].year){
                            cursor++;
                            articleGroup[cursor] = {
                                year: val.year,
                                articles: [val]
                            };
                        }else {
                            articleGroup[cursor].articles.push(val);
                        }
                    });
                
                    this.num = page.total;
                    this.$set(this,'articleGroup',articleGroup);
                    this.$set(this,'page',page);
                })
            }
        },
        data:function(){
            return {
                articleGroup:[
                    {
                        year:'',
                        articles:[],
                    }
                ],
                num:0,
                page:{},
                active: this.$route.params.active || 1
            }
        },
        watch:{
            $route: function (to, form) {
                this.active = to.params.active;
                this.getData();
            }
        },
        filters:{
            dateFormat: function (val) {
                return xm.dateFormat(val);
            }
        },
        computed:{
            countWord: function () {
                let type = this.$route.params.type;
                let name = this.$route.params.name;
                let word = '';
                switch (type){
                    case 'archives':
                        word = 'OK!目前共计 '+this.num+' 篇日志。继续努力。';
                        break;
                    case 'category':
                        word = name + ' 分类。';
                        break;
                    case 'tag':
                        word = name + ' 标签';
                        break;
                    default :
                        word = '程序又出BUG啦~~~';
                }
            
                return word;
            
            }
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss">
    @import "../style/import";
    .archives {
        font-size: 16px;
        border-left: 4px solid $gray;
        
        @mixin archives-dot($r) {
            &:before {
                width: $r;
                height: $r;
                border-radius: $r;
                left: -($r+4)/2;
            }
        }
        
        %archives {
            position: relative;
            
            padding-left: $basepadding;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                
                top: 0;
                bottom: 0;
                margin: auto 0;
                
                background: #aaa;
            }
        }
        
        &_count {
            @extend %archives;
            position: relative;
            top: -0.5em;
            
            @include archives-dot(12px);
        }
        
        &_title {
            font-weight: normal;
            padding-top: $basepadding*2;
            padding-bottom: $basepadding*2;
            
            @include text-lg;
            @include archives-dot(10px);
            @extend %archives;
        }
        
        &_item {
            line-height: 3em;
            color: #666;
            border-bottom: 2px dashed $gray;
            
            @extend %archives;
            @include archives-dot(6px);
            
            &:hover {
                color: #000;
                &:before {
                    background: $dark;
                }
            }
        }
        &_link {
            @include block;
            @include text-overflow;
        }
        &_date{
            font-size: 12px;
            margin-right: 15px;
        }
    }

</style>



