<template>
    <div :class="['page','container']">
        <div class="classify">
            <div class="classify_hd">
                当前共 {{categories.length}} 个分类
            </div>
            <div class="category">
                <router-link
                    :to="{name:'articleList',params:{type:'category',name:category.category || 'tmp',active:1}}"
                    class="category_item"
                    v-for="category in categories"
                    v-bind:key="category"
                >{{category.category}} ({{category.category_num}})</router-link >
            
            </div>
        </div>
        <div class="classify">
            <div class="classify_hd">
                当前共 {{tagsNum}} 个标签
            </div>
            <div class="tag">
                <router-link
                    :to="{name:'articleList',params:{type:'tag',name:tag || 'tmp',active:1}}"
                    :class="['hover-highlight','tag_item',{'text-xs':tag_num<=1},{'text-sm':tag_num>1 && tag_num <=3},{'text-md':tag_num>3 && tag_num<=6},{'text-lg':tag_num>6}]"
                    v-for="(tag_num,tag) in tags"
                    v-bind:key="tag_num"
                >{{tag}}</router-link >
            
            </div>
        </div>
    </div>
</template>
<script>
    export default{
        name:"tags",
        mounted:function(){
            this.$http.get('blog/tags').then((res)=>{
                return res.json();
            }).then((res)=>{
                this.$set(this,'categories',res.categories);
                // 暂时没有想到如何在数据库处理标签数据，因此目前只能采取这种折中的办法
                let tags = {};
                let tagsNum = 0;
                res.tags.forEach((val)=>{
                    let sigleTag = val['tags'].split(',');
                    sigleTag.forEach((val)=>{
                        val = val.trim();
                        if (val in tags) {
                            tags[val]++;
                        }else {
                            tags[val] = 1;
                            tagsNum++;
                        }
                    });
                });
                this.$set(this,'tags',tags);
                this.$set(this,'tagsNum',tagsNum);
            });
        },
        data:function(){
            return {
                categories:[],
                tags:{},
                tagsNum:0
            }
        },
    }
</script>
<style lang="scss" rel="stylesheet/scss">
    @import "../style/import";

    .classify {
    
        margin-bottom: $basemargin*5;
        text-align: center;
        &_hd {
            margin-bottom: $basemargin;
            @include text-md;
        }
    
    }
    .category {
    
        &_item {
            display: inline-block;
            padding: 5px 10px;
            margin: 10px 5px;
            border: 1px solid $dark;
        
            @include text-sm;
            &:hover {
                background-color: $dark;
                color: #fff;
                border: 1px solid $dark;
            }
        }
    }
    .tag {
        word-wrap: break-word;
        &_item {
            display: inline-block;
            margin: 10px;
            &.text-xs {
                font-size: 12px;
            }
            &.text-sm {
                font-size: 15.6px;
            }
            &.text-md {
                font-size: 22.8px;
            }
            &.text-lg {
                font-size: 30px;
            }
        }
    }

</style>



